import userExtra from '../../api/group/userExtra'
const state = {
  token:'',
  userExtraList:[]
}
// getters
const getters = {}

// actions
const actions = {
  async getUserExtra({ commit, state }) {

    let res = await userExtra(state.token).userExtraGet();
    commit('setUserExtra', res.data.data);
  },
}
// mutations
const mutations = {
  setUserExtra(state, userExtraList) {
    state.userExtraList = userExtraList;
  },
  setToken(state, token) {
    state.token = token;
  },
}
export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}



